import { Field } from 'mint-ui';
import { Picker } from 'mint-ui';
import { DatetimePicker } from 'mint-ui';
import addre from 'assets/address/addressinfo';
import cTitle from 'components/title';
import { mapState, mapMutations } from 'vuex';
const address = addre;
export default {
  data() {
    return {

      provinceoptions: [],
      cityoptions: [],
      regionoptions: [],
      provicevalue: '请选择省份',
      cityvalue: '请选择市区',
      regionvalue: '请选择辖区',
      bind_btn: '绑定手机',
      pickerVisible: '',
      year_value: '',
      month_value: '',
      day_value: '',
      info_form: {
        //用户姓名
        realname: '芸众',
        //联系电话
        telephone: '',
        //绑定电话
        mobile: '',
        //微信
        wx: '',
        //支付宝
        apipay: '',
        //生日
        birthday: '',
        //支付宝名字
        apipay_name: '',
        //性别
        gender: '0',
        avatar: '',
        city_name: '',
        address: '',
        // area_name	是	string	区名称
        area_name: '',
        province_name: '',
        // province	是	int	省ID
        province: '',
        // city	是	int	市ID
        city: '',
        // area	是	int	区ID
        area: ''
      },
      popupVisible: false,
    }
  },

  methods: {
    onAddressChange(picker, values) {
      picker.setSlotValues(1, address[values[0]]);
      this.addressProvince = values[0];
      this.addressCity = values[1];
      console.log(this.addressProvince, this.addressCity);
    },
    handleAvatarScucess(res, file) {
      this.info_form.avatar = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    //提交个人信息
    submitInfo() {
      var that = this;
      $http.post('member.member.updateUserInfo', { data: this.info_form }, '正在提交数据...').then(function (response) {
        console.log(response.data);
        if (response.result == 1) {
          var myData = response.data;

        }
      }, function (response) {
        // error callback
      });

    },
    handleChange(value) {
      console.log(value);

    },
    //选择省份的响应方法
    selectProviceHandle(value) {

      if (this.$route.params.item.province_name == value) {
        //首次
        this.initCityData(this.$route.params.item.city_name);

      } else {
        this.cityvalue = '请选择市区';
        this.regionvalue = '请选择辖区';
        this.info_form.province = value.id;
        this.info_form.province_name = value.areaname;
        for (let i = 0; i < this.$store.state.city.length; i++) {
          let item = this.$store.state.city[i];
          if (item.parentid == value.id) {
            this.cityoptions.push(item);
            // = this.cityoptions[0].areaname;
          }

        }

      }


      //this.form.province = value;
    },
    //选择市的响应方法
    selectCityHandle(value) {
      console.log(value);
      if (value == this.$route.params.item.city_name) {
        this.initDistrictData(this.$route.params.item.area_name);

      } else {
        this.info_form.city = value.id;
        this.info_form.city_name = value.areaname;
        this.regionoptions = [];
        for (let i = 0; i < this.$store.state.district.length; i++) {
          let item = this.$store.state.district[i];

          if (item.parentid == value.id) {
            this.regionoptions.push(item);
            //this.regionvalue = this.regionoptions[0].areaname;

          }

        }

      }

    },
    //选择市辖区的响应方法
    selectRegionHandle(value) {
      //console.log(value.areaname);
      this.info_form.area = value.id;
      this.info_form.area_name = value.areaname;
    },
    initCityData(city) {
      //通过省份的id找到相应的市
      this.cityoptions = [];
      for (let i = 0; i < this.$store.state.city.length; i++) {
        let item = this.$store.state.city[i];
        if (this.$route.params.item.province == item.parentid) {
          if (item.areaname == city) {
            this.cityvalue = city;
            this.info_form.city = item.id;
            this.info_form.city_name = item.areaname;

          }
          this.cityoptions.push(item);
        }


      }


    },
    initDistrictData(district) {
      //通过市的id找到相应的辖区
      this.regionoptions = [];
      for (let i = 0; i < this.$store.state.district.length; i++) {
        let item = this.$store.state.district[i];
        if (this.$route.params.item.city == item.parentid) {
          if (item.areaname == district) {
            this.regionvalue = district;
            this.info_form.area = item.id;
            this.info_form.area_name = item.areaname;

          }
          this.regionoptions.push(item);
        }
      }

    },
    bindTel() {
      this.$router.push({ name: "editmobile", params: { num: this.info_form.mobile, myparent: this } });
    }
  },
  //计算属性，更新数据
  computed: {


  },
  mounted() {
    var item = this.$route.params.item;
    if (this.$store.state.memberstate.member_model.gender == 1) {
      this.info_form.gender = '1';
    } else {
      this.info_form.gender = '0';
    }
    //this.info_form.gender = this.$store.state.memberstate.member_model.gender;
    this.info_form.alipay = this.$store.state.memberstate.member_model.alipay;
    this.info_form.realname = this.$store.state.memberstate.member_model.nickname;
    this.info_form.mobile = item.mobile;
    this.info_form.telephone = item.mobile;
    this.info_form.address = item.address;
    this.info_form.apipay_name = item.apipay_name;
    this.info_form.birthday = item.birthday;
    this.info_form.alipay = item.apipay;
    this.info_form.avatar = this.$store.state.memberstate.member_model.avatar;
    if (this.info_form.mobile) {
      this.bind_btn = '修改手机';
    } else {
      this.bind_btn = '绑定手机';

    }

    this.$on('editmobile', (mobile) => {
      this.info_form.mobile = mobile;
      this.info_form.telephone = mobile;
    });

  },
  activated() {
    var item = this.$route.params.item;
    if (item) {
      alert(item.province_name);
      this.provinceoptions = this.$store.state.province;
      this.provicevalue = item.province_name;

    }







  },
  components: { cTitle }
}


